body .website__ .uppy-Dashboard-inner {
       max-width: 100%;
}

body .website__ .uppy-Dashboard-browse:before {
       display: none;
}

body .website__ .uppy-Dashboard-AddFiles-title {
       padding: 0 10px;
       font-size: 12px;
}

.icon-bg-gradient {
       background-image: linear-gradient(to right top, #4e69ed, #6264ef, #755eef, #8757ef, #984eed);
}

.embed_size_ .selectpicker__control {
       border: none !important;
       border-top-right-radius: 3px;
       border-bottom-right-radius: 3px;
}

.tag_iframe_preview {
       height: 500px;
}

.tag_iframe_preview iframe {
       border: none;
       transform: scale(0.65);
       width: 153%;
       margin-left: -26.5%;
       height: 750px;
       margin-top: -130px;
       margin-bottom: -130px;
}

.tagshop {
       position: relative;
       z-index: 1;
}